.scroll-down{
    position: fixed;
    top: 50px;
    left: 50px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: blue;

}
.scroll-up{
    position: fixed;
    right: 50px;
    bottom: 50px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: blue;
}

